import React,{Component} from 'react'
import {View,StyleSheet,TouchableOpacity} from 'react-native'
import {Text} from 'react-native';
import Ptext from '../../../common/Ptext.js'
import Button from '../../../common/Button.js'

class EditedSettings extends Component {
  render () {
    return (
      <View style={{marginBottom : 12}}>
        <View style={stedited.wrapper}>
          <View style={stedited.editValueWrapper}>
            <Ptext style={stedited.caption}>{this.props.item.title}</Ptext>
            <Ptext style={stedited.value}>{this.props.item.value}</Ptext>
          </View>
          <TouchableOpacity onPress={this.props.onPress} style={stedited.button_edit}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" style={{alignSelf : 'flex-end'}}>
            <path d="M13.4619 8.02218L15.903 10.4632L9.72394 16.6423L7.28425 14.2012L13.4619 8.02218ZM17.7553 7.43346L16.6666 6.34483C16.2459 5.92412 15.5628 5.92412 15.1406 6.34483L14.0978 7.38763L16.5389 9.8287L17.7553 8.61234C18.0816 8.28601 18.0816 7.75977 17.7553 7.43346ZM6.00679 17.6323C5.96237 17.8322 6.14288 18.0114 6.34283 17.9628L9.063 17.3032L6.62331 14.8622L6.00679 17.6323Z" fill="#B4BBC2"/>
            </svg>
          </TouchableOpacity>
        </View>
        <View style={{width:'100%',borderColor : '#F1F3F5',borderWidth : 1, height : 2}}/>
      </View>
    )
  }
}

const stedited = StyleSheet.create({
  wrapper : {
    flexDirection : 'row',
    justifyContent : 'flex-start',
    alignItems : 'baseline',
    width : '100%',
  },
  editValueWrapper : {
    flexDirection : 'row',
    flexWrap : 'wrap',
    justifyContent : 'flex-start',
    alignItems : 'flex-start',
    flexGrow : 1,
    flex : 15,
    width : '99%',
  },
  caption : {
    fontWeight : 'bold',
    fontSize : 14,
    lineHeight : 16,
    minWidth : 175,
    flex : 1,
    textAlign : 'left',
    minHeight : 24,
  },
  value : {
    fontWeight : 'normal',
    fontSize : 14,
    lineHeight : 16,
    flex : 5,
    minHeight : 24,
    minWidth : 175,
    textAlign : 'left',
  },
  button_edit : {
    minWidth : 48,
    flex : 0.1,
    alignSelf : 'center',
  },
  button : {
    backgroundColor : '#fff',
    borderColor : '#b4bbc2',
    margin : 0,
    marginTop : 16,
    minWidth : 95,
    height : 32,
  }
});

class ActionSettings extends Component {
  render () {
    return (
      <View style={{marginBottom : 12}}>
        <View style={[stedited.wrapper,{flexDirection : 'column'}]}>
          <Ptext style={stedited.caption}>{this.props.item.title}</Ptext>
          <Ptext style={stedited.value}>{this.props.item.hint}</Ptext>
          <Button title={'Make it'} style={stedited.button}/>
        </View>
        <View style={{width:'100%',borderColor : '#F1F3F5',borderWidth : 1, height : 2}}/>
      </View>
    )
  }
}

class TabSettings extends Component {

  render () {
    return (
      <View style={styles.wrapper}>
        <EditedSettings item={{title:'E-mail',value:'supermail@mail.com'}}/>
        <EditedSettings item={{title:'',value:'+79112223344'}}/>
        <ActionSettings item={{title:' ',hint : '     ,   ,     , ,   .        .'}}/>
        <ActionSettings item={{title:' ',hint : '      .      .'}}/>
        <Text>Settings</Text>
      </View>
    )
  }
}

const styles = StyleSheet.create({
  wrapper : {
    width : '95%',
    alignSelf : 'center',
    alignItems : 'flex-start',
    justifyContent : 'flex-start',
  }

});

export default TabSettings;
